.settings-popover .ant-popover-inner-content,
notification-popover .ant-popover-inner-content {
  color: white;
  border-radius: 4px !important;
}
.settings-popover .ant-popover-title,
notification-popover .ant-popover-title {
  min-height: 20px !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  font-weight: 600;
}
.settings-popover.ant-popover-placement-bottomLeft > .ant-popover-content > .ant-popover-arrow,
notification-popover.ant-popover-placement-bottomLeft > .ant-popover-content > .ant-popover-arrow {
  border-top-color: #3e3f61 !important;
  border-left-color: #3e3f61 !important;
}
