.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.App-link {
  color: #2e7bfa;
}
a {
  color: #1f7afa;
}
a:hover {
  color: #00aef3;
}
.ant-input {
  border: none;
  height: 40px;
  border-radius: 4px;
  font-weight: 600;
  font-size: 0.9rem;
  text-align: right;
  color: #eeeeee;
  background-color: #292c42;
  padding-left: 16px;
}
.ant-input-affix-wrapper .ant-input-suffix {
  background-color: #292c42;
  min-height: 100%;
}
.ant-input-password-icon {
  color: #515a6b !important;
}
.ant-input-password-icon :hover {
  color: #eeeeee !important;
}
.ant-input-password input {
  letter-spacing: 1px;
}
.ant-input[disabled] {
  background-color: #292c42;
  color: #cdcdcde0;
}
.ant-checkbox-inner {
  background: #292b42 !important;
  border-color: #2e7bfa !important;
}
.ant-checkbox.ant-checkbox-checked .ant-checkbox-inner {
  background: #2e7bfa !important;
}
.ant-notification,
.ant-notification-notice-message {
  color: #cdcdcd !important;
  font-size: 0.9rem !important;
}
.ant-notification-notice-description {
  color: #696a9b !important;
  font-size: 0.85rem !important;
}
.ant-notification-close-icon {
  color: #696a9b;
}
.nav-tab-theme-button:hover {
  color: #eeeeee !important;
}
.ant-form-item-label {
  line-height: 24px;
}
.ant-form-item {
  margin-bottom: 0px;
}
.ant-list-bordered {
  border: none;
}
.ant-switch {
  background-color: #bdbdbd;
}
li.ant-pagination-disabled.ant-pagination-prev a.ant-pagination-item-link {
  background-color: #292c42;
  border-style: none;
  color: #bdbdbd;
}
li.ant-pagination-disabled.ant-pagination-prev a.ant-pagination-item-link :hover {
  color: #bdbdbd;
}
li.ant-pagination-item {
  background: #292c42;
  border-style: none;
}
li.ant-pagination-item:hover {
  background: #2e7bfa !important;
}
li.ant-pagination-item a {
  color: #bdbdbd;
}
li.ant-pagination-item-active {
  background: #2e7bfa;
  border-style: none;
}
.ant-pagination.mini .ant-pagination-item {
  margin: 2px;
}
li.ant-pagination-next a.ant-pagination-item-link {
  background-color: #292c42;
  border-style: none;
  color: #bdbdbd;
}
li.ant-pagination-next a.ant-pagination-item-link :hover {
  color: #bdbdbd;
}
li.ant-pagination-next :hover {
  color: #bdbdbd;
}
li.ant-pagination-next :focus {
  color: #bdbdbd;
}
li.ant-pagination-prev a.ant-pagination-item-link {
  background-color: #292c42;
  border-style: none;
  color: #bdbdbd;
}
li.ant-pagination-prev a.ant-pagination-item-link :hover {
  color: #bdbdbd;
}
li.ant-pagination-prev :hover {
  color: #bdbdbd;
}
li.ant-pagination-prev :focus {
  color: #bdbdbd;
}
li.ant-pagination-disabled.ant-pagination-next a.ant-pagination-item-link {
  background-color: #292c42;
  border-style: none;
  color: #bdbdbd;
}
li.ant-pagination-disabled.ant-pagination-next a.ant-pagination-item-link :hover {
  color: #bdbdbd;
}
.ant-table-content {
  background: #292b42 !important;
}
.ant-table-pagination.ant-pagination {
  padding: 4px 0px !important;
  margin: auto !important;
  text-align: center !important;
}
.ant-modal {
  top: 48px;
}
.ant-modal-header {
  background-color: #3e3f61;
  text-align: center;
  border-bottom: none;
  padding: 14px 24px;
}
.ant-modal-content {
  background-color: #2f304d;
}
.ant-modal-body {
  padding: 18px 60px;
}
.ant-modal-close-x {
  color: #696a9b;
  width: 54px;
  height: 54px;
  line-height: 54px;
}
.ant-input,
.ant-form *,
.ant-pagination-item {
  font-family: Montserrat, sans-serif !important;
}
.ant-pagination-item {
  font-size: 0.8rem !important;
}
.ant-pagination-item-link {
  color: #696a9b !important;
}
.ant-pagination-jump-prev .ant-pagination-item-container .ant-pagination-item-ellipsis {
  color: #696a9b;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.ant-pagination-jump-prev:hover .ant-pagination-item-ellipsis {
  opacity: 1;
  color: #bdbdbd;
}
.ant-pagination-jump-prev:focus .ant-pagination-item-ellipsis {
  opacity: 1;
}
.ant-pagination-jump-next .ant-pagination-item-container .ant-pagination-item-ellipsis {
  color: #696a9b;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.ant-pagination-jump-next:hover .ant-pagination-item-ellipsis {
  opacity: 1;
  color: #bdbdbd;
}
.ant-pagination-jump-next:focus .ant-pagination-item-ellipsis {
  opacity: 1;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.ant-dropdown-menu {
  padding: 0px;
}
.ant-dropdown-menu-item {
  font-size: 0.9rem;
}
.ant-dropdown-menu-submenu-arrow {
  display: none;
}
.ant-dropdown-menu-item-divider,
.ant-dropdown-menu-submenu-title-divider {
  margin: 0px;
  background-color: #292c42;
}
.ant-popover-content {
  box-shadow: 0 0 0 5000px rgba(0, 0, 0, 0.45) !important;
}
.ant-popover-inner-content {
  background: #292b42;
}
.ant-popover-title {
  border-bottom: none;
  color: #cdcdcde0;
  background: #3e3f61;
  font-size: 0.85rem;
}
.marketSelection.ant-popover-placement-bottom,
.marketSelection.ant-popover-placement-bottomLeft,
.marketSelection.ant-popover-placement-bottomRight {
  padding-top: 0px;
}
.ant-menu-dark.ant-menu-horizontal > .ant-menu-submenu {
  background: #292b42 !important;
}
.ant-layout-sider-children {
  background: #292b42;
}
